<template>
  <v-app>
    <v-toolbar app color='primary'>
      <v-toolbar-title v-t="'title'"></v-toolbar-title>
    </v-toolbar>
    <v-content v-if="loaded">
      <v-layout row>
        <v-flex xs12 sm6 offset-sm3>
          <v-card>
            <v-list three-line subheader v-if="success">
              <v-list-tile>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'address'"></v-list-tile-title>
                  <v-list-tile-sub-title>{{ address}}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider></v-divider>
              <v-list-tile v-if="amountType != 'VIPS' && amountExt > 0">
                <v-list-tile-content>
                  <v-list-tile-title v-t="'amount_fiat'"></v-list-tile-title>
                  <v-list-tile-sub-title>{{ amountExt }} {{ amountType }}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider v-if="amountType != 'VIPS' && amountExt > 0"></v-divider>
              <v-divider v-if="amount > 0"></v-divider>
              <v-list-tile v-if="amount > 0">
                <v-list-tile-content>
                  <v-list-tile-title v-t="'amount'"></v-list-tile-title>
                  <v-list-tile-sub-title>{{ amount }} VIPS</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider v-if="amount > 0"></v-divider>
              <v-list-tile v-if="label != ''">
                <v-list-tile-content>
                  <v-list-tile-title v-t="'label'"></v-list-tile-title>
                  <v-list-tile-sub-title>{{ label}}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider v-if="label != ''"></v-divider>
              <v-list-tile v-if="message != ''">
                <v-list-tile-content>
                  <v-list-tile-title v-t="'message'"></v-list-tile-title>
                  <v-list-tile-sub-title>{{ message}}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider v-if="message != ''"></v-divider>
            </v-list>
            <v-card-title v-if="!success">
              <p style="width: 100%; text-align: center" v-t="'invalid'">Invalid VIPSTARCOIN BIP21 URI!</p>
            </v-card-title>
            <v-container>
              <v-layout row v-if="success">
                <v-flex xs12>
                  <v-btn dark block color="primary" :href="uri" v-t="'open_wallet'"></v-btn>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn dark block color="secondary" href="https://www.vipstarcoin.jp/#wallet" v-t="'download_wallet'"></v-btn>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-btn dark block color="secondary" href="https://1drv.ms/u/s!ApSyFjxINj72g8N66nF_O_ImrtUjFg" v-t="'download_regvips'"></v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-content>
  </v-app>
</template>

<style scoped>
a.btn.primary {
  color: #000000;
}
</style>

<script>
import bip21 from 'bip21'
import axios from 'axios'
import BigNumber from 'bignumber.js'

const TICKER = {
  ID: 2688,
  URL: 'https://api.coinmarketcap.com/v2/ticker/'
}

export default {
  data () {
    return {
      loaded: false,
      success: false,
      uri: '',
      address: '',
      amount: 0,
      amountExt: 0,
      amountType: 'VIPS',
      label: '',
      message: ''
    }
  },
  mounted: function () {
    this.uri = location.hash.replace(/^#/, '')
    try {
      let dec = bip21.decode(this.uri, 'vipstarcoin')

      this.address = dec.address
      if (dec.options.amountType && dec.options.amountType != 'VIPS' && dec.options.amountExt) {
        this.amountExt = dec.options.amountExt
        this.amountType = dec.options.amountType
        this.calcAmount(dec.options.amountExt, dec.options.amountType).then(amount => {
          this.amount = amount
          let opt = dec.options
          opt.amount = amount
          this.uri = bip21.encode(
            this.address,
            opt,
            'vipstarcoin'
          )
        })
      } else {
        if (dec.options.amount) this.amount = dec.options.amount
      }
      if (dec.options.label) this.label = dec.options.label
      if (dec.options.message) this.message = dec.options.message
      this.success = true
    } catch (e) {
    }

    this.loaded = true
  },
  methods: {
    calcAmount: function (amountExt, type) {
      let uri = `${TICKER.URL}${TICKER.ID}/?convert=${type}`
      return axios.get(uri).then(resp => {
        let quote = resp.data.data.quotes[type]
        if (quote === null) {
          return Promise.reject(new Error(`${type} does'nt supported`))
        } else {
          let fiat = new BigNumber(amountExt)
          let rate = new BigNumber(quote.price)
          let amount = fiat.dividedBy(rate)
          let r = 0
          if (amount.toString() !== 'NaN') {
            r = amount.decimalPlaces(8).toNumber()
          }
          return Promise.resolve(r)
        }
      }).catch(error => {
        console.error(error)
      })
    }
  }
}
</script>



